import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import shopAdd from './shop-add.vue';
import pageView from '@/components/common-ui/page-view.vue';
import { EventBus } from '@/until/event-bus';
let AppList = class AppList extends Vue {
    constructor() {
        super(...arguments);
        this.classId = '';
        this.className = '';
        this.shopList = [];
        //5693c8b0-f97f-11ea-8165-3111824eebe8
        this.showAdd = false;
        this.shopObj = {
            name: '',
            distribution: false,
            imgUrlList: []
        };
    }
    getShopList() {
        let businessId = this.$store.state.userInfo.business;
        const toast = this['$createToast']({
            time: 0,
            txt: '商店获取中...'
        });
        toast.show();
        this.$store.dispatch('getShopList', { page: 1, businessId }).then((response) => {
            if (response.success) {
                let list = response.data.list;
                this.$set(this, 'shopList', list);
                toast.hide();
            }
        });
    }
    addShop() {
        this.$router.push({
            name: 'shopAdd'
        });
    }
    addCancel() {
        this.showAdd = false;
    }
    saveSuccess(type, data) {
        if (type === 'add') {
            this.shopList.push(data);
        }
        else {
            let Index;
            this.shopList.some((item, index) => {
                if (data.id === item.id) {
                    Index = index;
                    return true;
                }
            });
            this.$set(this.shopList, Index, data);
        }
        this.addCancel();
    }
    editShop(item) {
        this.$router.push({
            name: 'shopAdd', params: {
                id: item.id,
                name: item.name,
                distribution: item.distribution,
                imgUrlList: item.imgUrlList,
                businessId: item.businessId
            }
        });
    }
    mounted() {
        this.getShopList();
        EventBus.$on('shopList', (msg) => {
            this.saveSuccess(msg.type, msg.data);
        });
    }
    toCommodityClass(item) {
        this.$router.push({ path: '/shopList/commodityManageClass', query: { shopId: item.id } });
    }
};
AppList = __decorate([
    Component({
        components: {
            page,
            shopAdd,
            pageView
        }
    })
], AppList);
export default AppList;
